<template>
  <div id="category">
    <button id="btnBabanas" class="button" @click="allFalse('bananas')">Cambures</button>
    <div id="subBabanas" class="subCategory" v-show="subBabanas">
      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'bananas' }}"
      >Sub Categoria 1</router-link>
      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'bananas' }}"
      >Sub Categoria 2</router-link>
      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'bananas' }}"
      >Sub Categoria 3</router-link>
    </div>

    <button id="btnCoconuts" class="button" @click="allFalse('coconuts')">Cocos</button>
    <div id="subCoconuts" class="subCategory" v-show="subCoconuts">
      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'coconuts' }}"
      >Sub Categoria 1</router-link>
      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'coconuts' }}"
      >Sub Categoria 2</router-link>
    </div>

    <button id="btnApples" class="button" @click="allFalse('apples')">Manzanas</button>
    <div id="subApples" class="subCategory" v-show="subApples">
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'apples' }}">Sub Categoria 1</router-link>
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'apples' }}">Sub Categoria 2</router-link>
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'apples' }}">Sub Categoria 3</router-link>
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'apples' }}">Sub Categoria 4</router-link>
    </div>

    <button id="btnPears" class="button" @click="allFalse('pears')">Peras</button>
    <div id="subPears" class="subCategory" v-show="subPears">
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'pears' }}">Sub Categoria 1</router-link>
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'pears' }}">Sub Categoria 2</router-link>
    </div>

    <button id="btnPineaples" class="button" @click="allFalse('pineaples')">Piñas</button>
    <div id="subPineaples" class="subCategory" v-show="subPineaples">
      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'pineaples' }}"
      >Sub Categoria 1</router-link>

      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'pineaples' }}"
      >Sub Categoria 2</router-link>

      <router-link
        :to="{ name: 'ProductsCategory', params: { category: 'pineaples' }}"
      >Sub Categoria 3</router-link>
    </div>

    <button id="btnGrapes" class="button" @click="allFalse('grapes')">Uvas</button>
    <div id="subGrapes" class="subCategory" v-show="subGrapes">
      <router-link :to="{ name: 'ProductsCategory', params: { category: 'grapes' }}">Sub Categoria 1</router-link>

      <router-link :to="{ name: 'ProductsCategory', params: { category: 'grapes' }}">Sub Categoria 2</router-link>

      <router-link :to="{ name: 'ProductsCategory', params: { category: 'grapes' }}">Sub Categoria 3</router-link>

      <router-link :to="{ name: 'ProductsCategory', params: { category: 'grapes' }}">Sub Categoria 4</router-link>
    </div>

    <div
      id="subMessage"
      class="subCategory"
      v-show="!subBabanas && !subCoconuts && !subApples && !subPears && !subPineaples && !subGrapes"
    >
      <p>"Planta un árbol y estarás sembrando conciencia"</p>
      <span>Desconocido</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "MenuCategory",
  data() {
    return {
      subBabanas: false,
      subCoconuts: false,
      subApples: false,
      subPears: false,
      subPineaples: false,
      subGrapes: false
    };
  },
  methods: {
    allFalse(itemTrue) {
      this.subBabanas = false;
      this.subCoconuts = false;
      this.subApples = false;
      this.subPears = false;
      this.subPineaples = false;
      this.subGrapes = false;

      switch (itemTrue) {
        case "bananas":
          this.subBabanas = true;
          break;
        case "coconuts":
          this.subCoconuts = true;
          break;
        case "apples":
          this.subApples = true;
          break;
        case "pears":
          this.subPears = true;
          break;
        case "pineaples":
          this.subPineaples = true;
          break;
        case "grapes":
          this.subGrapes = true;
          break;
        default:
          break;
      }
    }
  }
};
</script>

<style scoped>
/********* Category ********/
#category {
  width: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  margin-top: 10px;
}
#category .button {
  grid-column: 1 / 2;
  width: 7rem;
  height: 1.8rem;
  text-align: center;
  border: none;
  background-color: transparent;
  color: white;
  font-weight: bold;
  line-height: 1.5rem;
  margin: 5px auto;
  border-radius: 10px;
  cursor: pointer;
  text-decoration: none;
}

#category #btnBabanas {
  background-color: rgb(255, 196, 0);
  color: #555555;
  border-bottom: 5px solid rgb(226, 174, 2);
}

#category #btnCoconuts {
  background-color: brown;
  border-bottom: 5px solid rgb(105, 28, 28);
}

#category #btnApples {
  background-color: red;
  border-bottom: 5px solid rgb(218, 1, 1);
}

#category #btnPears {
  background-color: green;
  border-bottom: 5px solid rgb(0, 77, 0);
}

#category #btnPineaples {
  background-color: rgb(255, 238, 0);
  color: #555555;
  border-bottom: 5px solid rgb(214, 200, 0);
}

#category #btnGrapes {
  background-color: purple;
  border-bottom: 5px solid rgb(75, 0, 75);
}

/********* Subcategory / Filters ********/
.subCategory {
  grid-column: 2 / 3;
  grid-row: 2 / 5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  height: 100%;
  width: 100%;
  border-radius: 10px;
}
#subBabanas {
  border: 5px solid rgb(226, 174, 2);
}
#subCoconuts {
  border: 5px solid rgb(105, 28, 28);
}
#subApples {
  border: 5px solid rgb(218, 1, 1);
}
#subPears {
  border: 5px solid rgb(0, 77, 0);
}
#subPineaples {
  border: 5px solid rgb(214, 200, 0);
}
#subGrapes {
  border: 5px solid rgb(75, 0, 75);
}

#subMessage {
  border: 5px solid rgb(153, 238, 26);
}
#subMessage > p {
  font-weight: bolder;
  font-size: 1.2rem;
  line-height: 1.5rem;
  text-transform: uppercase;
  font-family: "Kaushan Script";
}
#subMessage > span {
  width: 75%;
  text-align: right;
  font-size: 0.7rem;
}
.subCategory > a {
  text-decoration: none;
}
/********* Responsive Design ********/
</style>
