<template>
  <nav id="navBar">
    <div id="searchBar">
      <form id="formSearchNav">
        <input id="inputSearch" type="text" name="search" placeholder="Buscar..." />
        <input id="btnSearch" type="submit" value="Buscar" />
      </form>

      <button id="btncategory" @click="showCategory= !showCategory">Categorías</button>
    </div>

    <PmMenuCategoryNav v-show="showCategory"></PmMenuCategoryNav>
  </nav>
</template>

<script>
import PmMenuCategoryNav from "@/components/MenuCategoryNav.vue";

export default {
  name: "NavbarProducts",
  components: {
    PmMenuCategoryNav
  },
  data() {
    return {
      showCategory: false,
      clickMenu: false
    };
  },
  mounted() {
    this.showMenuFun();
  },
  methods: {
    showMenuFun() {
      if (this.clickMenu == true) {
        this.clickMenu = false;
      }
      this.showCategory = false;
    }
  },
  watch: {
    clickMenu: "showMenuFun",
    $route() {
      this.showCategory = false;
    }
  }
};
</script>

