<template>
  <main id="content">
    <PmSidebar v-if="windowWidth > 1000"></PmSidebar>
    <PmNavbar v-else></PmNavbar>
    <PmListProducts :windowWidth="windowWidth" v-if="$route.name === 'ProductsCategory'"></PmListProducts>
    <PmHomeProducts :windowWidth="windowWidth" v-else-if="$route.name === 'Products'"></PmHomeProducts>
  </main>
</template>

<script>
import PmSidebar from "@/components/layout/SidebarProducts.vue";
import PmNavbar from "@/components/layout/NavbarProducts.vue";
import PmListProducts from "@/components/layout/ListProducts.vue";
import PmHomeProducts from "@/components/layout/HomeProducts.vue";

export default {
  name: "Products",
  components: {
    PmSidebar,
    PmListProducts,
    PmHomeProducts,
    PmNavbar
  },
  data() {
    return {
      windowWidth: window.innerWidth
    };
  },
  mounted() {
    window.addEventListener("resize", this.handleWindowResize);
  },
  methods: {
    handleWindowResize() {
      this.windowWidth = window.innerWidth;
    },
    responsive() {}
  },
  watch: {
    windowWidth: "handleWindowResize"
  }
};
</script>

